<template lang="html">
    <div class="list">
        <div class="search">
            <input type="text" v-model="keyword" @keydown="onSearch" placeholder="正则搜索">
        </div>
        <QianList class="qian-list" :qianList="qianList"></QianList>
    </div>
</template>

<script>
import QianList from './QianList';

import api from 'api';

export default {
    components: {
        QianList
    },
    mounted() {
        this.init();
    },
    data() {
        return {
            keyword: '',
            allQian: [],
            qianList: []
        };
    },
    methods: {
        init() {
            this.onQuery();
        },
        onQuery() {
            api.getAllQian().then(res => {
                if (!res.code) {
                    this.allQian = res.data;
                    this.qianList = this.allQian.slice();
                }
            });
        },
        onSearch(event) {
            this.keyword = this.keyword.trim();
            if (event.keyCode !== 13) {
                return false;
            }
            let keyRegx = new RegExp(this.keyword);
            this.qianList = this.allQian.reduce((a, qian) => {
                keyRegx.test(qian.title) && a.push(qian);
                return a;
            }, []);
        }
    }
};
</script>

<style lang="less">
.list {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    & > div {
        width: 100%;
    }
    .search {
        height: 3em;
        display: flex;
        align-items: center;
        input {
            width: 100%;
            margin: 0 1em;
            padding: 0;
            outline: none;
            border: none;
            border-bottom: 1px solid @infoColor;
        }
    }
    .qian-list {
        height: calc( 100% - 3em );
    }
}
</style>
